import { Row, Col } from "antd";
import { withTranslation, TFunction } from "react-i18next";
import { Slide } from "react-awesome-reveal";
import { Button } from "../../common/Button";
import { MiddleBlockSection, Content, ContentWrapper, MidList } from "./styles";
import { SvgIcon } from "../../common/SvgIcon";
import { MinPara, MinTitle } from "../ContentBlock/styles";

interface MiddleBlockProps {
  title: string;
  content: string;
  button: string;
  section: {
    title: string;
    content?: string;
    icon?: string;
  }[];
  t: TFunction;
}

const MiddleBlock = ({ title, content, button, section, t }: MiddleBlockProps) => {
  const scrollTo = (id: string) => {
    const element = document.getElementById(id) as HTMLDivElement;
    element.scrollIntoView({
      behavior: "smooth",
    });
  };
  return (
    <MiddleBlockSection>
      <Slide direction="up" triggerOnce>
        <Row justify="center" align="middle">
          <ContentWrapper>
            <Col lg={24} md={24} sm={24} xs={24}>
              <h6>{t(title)}</h6>
              <Content>{t(content)}</Content>
              <Row justify="space-between">
                    {typeof section === "object" &&
                      section.map(
                        (
                          item: {
                            title: string;
                            content?: string;
                            icon?: string;
                          },
                          id: number
                        ) => {
                          return (
                            <Col key={id} span={11}>
                              <MidList>{t(item.title)}</MidList>
                            </Col>
                          );
                        }
                      )}
              </Row>
              <Content>{t("These objectives guide use in protecting and promoting the health, safety, and well-being of workers, while also supporting business success and sustainability. To achieve these goals, we offer a range of services tailored to meet the needs of both workers and organizations.")}</Content>
              {button && (
                <Button name="submit" onClick={() => scrollTo("mission")}>
                  {t(button)}
                </Button>
              )}
            </Col>
          </ContentWrapper>
        </Row>
      </Slide>
    </MiddleBlockSection>
  );
};

export default withTranslation()(MiddleBlock);
